<template>
    <v-container fluid class="container" @click="timer_logout_set()">
        <layout-header/>
        <v-row class="contents" align="center" justify="center">
            <list-menu-list
                class="content-body"
                title="서킷트레이닝"
                description="운동을 시작하려면 해당 운동을 선택해주세요."
                :thumbnailList="thumbnailList"
            />
        </v-row>
    </v-container>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex'
import exercise from '@/mixins/exercise.js'
import motionCamera from '@/mixins/motionCamera.js'
import timer_logout from '@/mixins/timer_logout.js'

export default {
    name: 'Circut',
    mixins:[ exercise, timer_logout, motionCamera ],
    data:()=>({
        thumbnailList: []
    }),
    computed:{
        ...mapGetters([
            'GET_MENU_CIRCUIT_LIST',
            'GET_MENU_EX_RESULT_LIST',
        ])
    },
    async mounted() {
        await this.GetExResultList()
        await this.ACT_MENU_API_GET_CIRCUIT_LIST()
        .then(()=>{
            this.EditCircuitList()
        })
    },
    methods: {
        ...mapActions([
            'ACT_MENU_API_GET_EX_RESULT_LIST',
            'ACT_MENU_API_GET_CIRCUIT_LIST',
            'ACT_MENU_API_GET_CIRCUIT_PROGRAM',
        ]),
        ...mapMutations([
            'MU_MENU_CIRCUIT_LIST',
            'MU_PROGRAM_PLAY_TOTAL_SET'
        ]),
        async GetExResultList(){
            const exType = 5    // 5-서킷 (근력) 6-서킷 (스트레칭)
            await this.ACT_MENU_API_GET_EX_RESULT_LIST(exType)
        },
        async EditCircuitList(){
            let lastExerciseData
            let seochoExerciseData = []
            for( const item of this.GET_MENU_CIRCUIT_LIST){
                item.name  = item.exSortName
                item.link  = 'program'
                
                // 운동 이력 체크
                let check = this.GET_MENU_EX_RESULT_LIST.exList.find(list => list.exSortCode === item.exSortCode)
                if(check !== undefined) item.check = true

                // 이미지 추가
                item.image  = this.exercise_create_image(item.exSortCode)

                // program 추가
                let Program  = await this.ACT_MENU_API_GET_CIRCUIT_PROGRAM(item.exSortCode)
                for( const programItem of Program.subExList){
                    // motion mode check
                    programItem.motion      = this.motionCamera_checkExercise(programItem.subExSortCode)
                    programItem.image       = this.exercise_create_image(programItem.subExSortCode)
                    programItem.video_ex    = this.exercise_create_ex_video(programItem.subExSortCode)
                    programItem.video_rest  = this.exercise_create_rest_video(programItem.subExSortCode)
                    programItem.track_ex    = this.exercise_create_ex_track(programItem.subExSortCode)
                    programItem.track_rest  = this.exercise_create_rest_track(programItem.subExSortCode)
                    programItem.audio_rest  = this.exercise_create_rest_audio(programItem.subExSortCode)
                    programItem.name        = programItem.subExName
                }
                item.program = Program.subExList
                
                if(item.exSortCode === 6203 || item.exSortCode === 6204 || item.exSortCode === 7005){
                    // 서초구 운동 예외처리
                    seochoExerciseData.unshift(item)
                }else if(item.exSortCode === 6201){
                    // 마무리 스트레칭 메뉴 예외처리
                    lastExerciseData = item
                }else{
                    this.thumbnailList.push(item)
                }
            }

            // 서초구 운동 앞으로 추가
            if(seochoExerciseData.length !== 0){
                for( const item of seochoExerciseData){
                    this.thumbnailList.unshift(item)
                }
            }
            
            // 마무리 스트레칭 메뉴 뒤로 보내기
            if(lastExerciseData.length !== 0){
                this.thumbnailList.push(lastExerciseData)
            }

            this.MU_MENU_CIRCUIT_LIST(this.thumbnailList)
            // console.log(this.thumbnailList)
            
            // 서킷 Total Set 등록
            const playSet = 1
            this.MU_PROGRAM_PLAY_TOTAL_SET(playSet)
        },
    }
}
</script>

<style scoped lang="scss">
</style>